body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-color{
  background: #833ab4;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(right, #fcb045, #fd1d1d, #833ab4);
  background: linear-gradient(to left, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */  
}

.landing-grid{
  background: #833ab4;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(right, #fcb045, #fd1d1d, #833ab4);
  background: linear-gradient(to left, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */  
  text-align: center;
  position: fixed;
  top: 10;
  left: 0;
  width: 100%;
  height: 100%;
}

.avatar-img{
  height: 250px;
  margin-top: 5em;
}

.banner-text{
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;  
}

.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: white;  
}

.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p{
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 50%;
  margin: auto;
  -webkit-transition: 0.3s all ease-in-out;
  transition: 0.3s all ease-in-out;
}
.social-links a:hover{  
  -webkit-transform: scale(1.2);  
          transform: scale(1.2);
}
.social-links i{  
  color: white;
  font-size: 5em;
  padding: 8px;
}
.social-links i:hover{
  color: #aaa;
}
/* projects page start */
.projects-grid{
  margin: 40px !important;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  /* justify-content: space-between;   */
}

.card{
  margin: 30px !important;
}

/* project page end */
/* Contacts page css */
.contact-body{
  margin: auto;  
  /* position: fixed; */
  text-align: center;
  width: 100%;
  height: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 2em
}

.contact-grid{
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;    
  background: white;
  color: black;  
  margin-bottom: 20px;
}

.contact-grid h2{
  font-family: "Anton",sans-serif;
}

.contact-grid p{
  font-family: "Oxygen",sans-serif;
  font-weight: bold;
}

.contact-grid hr{
  border-top: 5px dotted black;
  margin: auto;
  width: 50%;
}

.contact-list{
  /* border:1px solid black; */
  padding: 0 22%;
}
.contact-list i{  
  font-size: 66px;  
  padding-right: 2rem;
}
/* Contacts page css */

/* Resume page css */
.resume-left-col a{
  text-decoration: none;
  -webkit-transition: 0.2s all ease-in-out;
  transition: 0.2s all ease-in-out;
}

.resume-left-col a:hover{
  color: #333;
  text-decoration: none;
}

.resume-right-col{
  background: #393235;
  color: white;
  padding: 2em;
}

.resume-right-col a{  
  text-decoration: none;  
  -webkit-transition: 0.2s all linear;  
  transition: 0.2s all linear;
}


.resume-right-col a:hover{
  color:#aaa;
  text-decoration: none;
  font-size: 1.2rem;
}
/* Resume page css */
@media only screen and (max-width: 800px) {
  .landing-grid{
    position: relative;
  }
  .social-links{
    -webkit-justify-content: space-around;
            justify-content: space-around;
  }
  .banner-text h1{
    font-size: 36px;        
  }
  .social-links i{    
    font-size: 3em;
    padding: 5px;
  }
  .projects-grid{
    -webkit-flex-direction: column;
            flex-direction: column;    
  }
  .projects-grid .card{
    margin-bottom: 20px !important;    
  }

  .projects-grid .card .link{
    text-decoration: none;
  }
  .contact-list{    
    padding: 0
  }
  .contact-list i{  
    font-size: 40px;
    /* padding-right: 2rem; */
  }  
}
